const recomendationsHeader = createHtmlElement('h2', '–•–∏—Ç –ø—Ä–æ–¥–∞–∂', 'recomendations__h2');
const recomendationsCardsWrap = createHtmlElement('div', '', 'recomendations__card-wrap');
